.headButton {
  background-color: green;
}

.ghostButton {
  background-color: transparent;
  border: 2px solid black;
  color: black;
  padding: 10px;
  transition: background-color 0.5s, color 0.5s, border-radius 0.5s;
}

.ghostButton:hover {
  background-color: black;
  border-radius: 10px;
  color: white;
}
