@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

.header-picture {
  background-image: url("../images/headerBackground.png");
  background-size: 100%;
  background-repeat: no-repeat;
  display: block;
  width: 100%;
  min-height: 500px;
  padding-top: 3%;
}

.header-logo {
  margin-left: auto;
  margin-right: auto;
  width: 20%;
  display: block;
}

.home-button {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  height: 120px;
  float: left;
  width: 16.66%;
  text-align: center;
  position: relative;
  border-right: 1px solid #616161;
}

.home-button .sub-title {
  position: absolute;
  bottom: 0;
  font-size: 20px;
  left: 0;
  right: 0;
  margin: auto;
  color: #ffffff;
}

.home-button .iconSprite {
  position: absolute;
  bottom: 50px;
  font-size: 20px;
  left: 0;
  right: 0;
  margin: auto;
}

.home-button:last-child {
  border-right: none;
}

.hot-item {
  background-color: #565656;
  border: 1px solid #717171;
  width: 25%;
  float: left;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

.hot-item .hot-item-image {
  width: 240px;
  height: 170px;
  overflow: hidden;
  position: relative;
}

.hot-item .hot-item-image img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hot-item .hot-item-info {
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  color: #ffffff;
  font-family: Arial;
  overflow: hidden;
}

.hot-item .hot-item-info .hot-item-name {
  float: left;
  margin: 0;
}

.hot-item .hot-item-info .hot-item-price {
  float: right;
  margin: 0;
}

.newsletter {
  clear: both;
  height: 188px;
}

.newsletter .newsletter-image {
  float: left;
  background-image: url("../images/patroon.jpg");
  background-repeat: repeat;
  height: 100%;
  width: 60%;
  text-align: center;
  color: #ffffff;
}

.newsletter .newsletter-image .newsletter-title {
  font-family: Choplin;
  font-size: 44px;
  margin-bottom: 0;
  margin-top: 25px;
}

.newsletter .newsletter-image .newsletter-subtitle {
  font-family: Choplin;
  font-size: 20px;
  margin-bottom: 0;
  margin-top: 25px;
}

.newsletter .newsletter-subscribe {
  float: left;
  width: 40%;
  height: 100%;
  background-color: #5f5f5f;
  padding: 35px;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  font-family: Arial;
}

.newsletter .newsletter-subscribe .subscribe-title {
  margin-top: 0px;
  color: #ffffff;
  font-size: 20px;
}

.newsletter .newsletter-subscribe .subscribe-subtitle {
  margin-top: 0px;
  color: #cecece;
  font-size: 14px;
}

.newsletter .newsletter-subscribe .newsletter-form {
  font-family: Arial;
  width: 100%;
}

.newsletter .newsletter-subscribe .newsletter-form .newsletter-email-input {
  padding: 12px;
  font-size: 16px;
  color: #000000;
  width: 70%;
  border: 1px solid #717171;
}

.newsletter .newsletter-subscribe .newsletter-form .newsletter-submit {
  padding: 12px;
  background-color: #323232;
  color: #ffffff;
  width: 20%;
  font-size: 16px;
  border: none;
}

.hot_items {
  width: 960px;
}
